/* Navbar */

.navbar {
  padding-bottom: 1px;
  /* border-bottom: solid 1px #e8e8e8; */
  background-color: white;
  box-shadow: 0 3px 6px -2px rgba(0, 0, 0, 0.12),
    0 6px 16px -11px rgba(0, 0, 0, 0.08);
  position: sticky;
  top: 0;
  /* margin-bottom: 20px; */
  z-index: 1009;
}

.nav-header {
  background-color: white !important;
  border-bottom: 0;
  padding: 0 10px 0 0px;
  height: 65px;
  position: sticky;
  top: 0;
  z-index: 1009;
}

.logo {
  margin-left: 55px;
  margin-right: 5px;
  width: 200px;
  float: left;
}

.logo a {
  display: inline-block;
  font-size: 20px;
  text-transform: capitalize;
}

.navbar-menu {
  /* width: calc(100% - 200px); */
  width: 100%;
  float: left;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-menu .ant-layout-header {
  padding: 0 20px;
}

.navbar-menu .ant-menu-horizontal {
  border-bottom: 0;
}

.navbar-menu .ant-menu-item {
  padding-bottom: 0px;
}

.navbar-menu .leftMenu {
  float: left;
}

.navbar-menu .rightMenu {
  float: right;
  display: flex;
  margin-left: 10px;
  justify-content: center;
  align-items: center;
}
.rightMenu .navSearch {
  width: 15rem;
  margin: 0 10px 0 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menuButton {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 32px;
  /* padding: 6px; */
  text-align: center;
  margin-top: 3px;
  margin-left: 5px;
  display: none;
  background: none;
  margin-right: 10px;
  /* background-color: #000; */
  color: #508201;
  border-color: #508201;
}

.ant-drawer-body {
  padding: 0;
}

.ant-drawer-body .ant-menu-horizontal > .ant-menu-item,
.ant-drawer-body .ant-menu-horizontal > .ant-menu-submenu {
  display: inline-block;
  width: 100%;
}

.ant-drawer-body .ant-menu-horizontal {
  border-bottom: none;
}

.ant-drawer-body .ant-menu-horizontal > .ant-menu-item:hover {
  border-bottom-color: transparent;
}

.navUsername {
  display: inline-block;
  margin-left: -1px;
}
.Dropdown {
  background: transparent !important;
}
.DropdownBtn {
  border: none;
  background: #fafafa !important;
}
.navMenu {
  background: none !important;
  display: none;
}
.navLoginMenu {
  margin-top: 5px;
  /* background-color: #000 !important; */
  height: 100% !important;
}
.navLoginBtn {
  background-color: #000 !important;
}
#searchBtn-searchButton-popup {
  background: #dafaa7;
}
#searchBtn {
  background: transparent;
  display: none;
}
#searchBtn svg {
  width: 17px;
  height: 17px;
}
/* Home Sidebar Css */
.filterMenuButton {
  display: none;
}
@media (max-width: 1000px) {
  .navSearch {
    display: none !important;
  }
  #searchBtn {
    display: block;
  }
}

@media (max-width: 767px) {
  .Dropdown {
    display: none !important;
  }
  .ant-menu-overflow-item-rest {
    display: none !important;
  }

  .navbar-menu {
    justify-content: end;
  }
  .menuButton {
    display: block;
    margin-right: 10px;
    margin-top: 15px;
  }

  .leftMenu,
  .rightMenu {
    display: none;
  }
  .logo {
    margin-left: 75px;
    /* margin-top: -10px; */
  }
  .logo a {
    margin-top: 0px;
    padding: 0;
    margin-left: -10px;
  }
  .logo a img {
    margin-top: 20px;
  }

  #brand-font {
    margin-bottom: 0;
  }

  .username {
    display: inline-block;
  }
  .navMenu {
    display: block;
  }

  /* Home Sidebar Css */
  .homeSidebar {
    display: none;
  }
  .filterMenuButton {
    display: block;
    margin-right: 5px;
    margin-left: 15px;
    border: none;
    color: #000;
    background-color: #ff9c0a;
  }
  .filterMenuButton:active {
    background-color: #dd8604;
  }
}

/* Navbar End */

